import {useEffect, useState} from 'react';
import {useSpring, animated} from 'react-spring';
import { Link, useLocation } from 'react-router-dom';
import { Close } from 'styled-icons/evil';
import styled from 'styled-components';

/** context **/
import { useAppState } from '../../context/app.context';

/** components **/
import StyledMenuLinks from '../Styled/StyledMenuLinks';

import { routeArr } from '../../routes/Routes';
import { defaultConfig, fade, translateHorizontal } from '../../transitions';

function Menu(props) {
  const { menuCollapsed, setMenuCollapsed, theme } = useAppState();
  const location = useLocation();
  const [hideMenu, setHideMenu] = useState(true);
  const menuContainerSpring = useSpring({
    ...fade({opacityEnd: menuCollapsed ? 0 : 1, opacityStart: menuCollapsed ? 0 : 0}),
    config: {
      tension: 100,
      friction: 10,
      clamp: true
    }
  });
  const menuSpring = useSpring({
    ...translateHorizontal({positionEnd: menuCollapsed ? 100 : 0, positionStart: menuCollapsed ? 100 : 100}),
    config: {
      tension: 100,
      friction: 10,
      clamp: true
    },
    onRest: () => setHideMenu(menuCollapsed)
  });

  function handleClose() {
    setMenuCollapsed(true);
  }

  return (
    <StyledMenu className={hideMenu ? 'hidden' : ''}>
      <animated.div className="overlay" style={menuContainerSpring} onClick={handleClose}/>
      <animated.div className="menu" style={menuSpring}>
        <div className="header">
          <Close className="close-button" onClick={handleClose} />
        </div>
        <StyledMenuLinks>
          {routeArr.map(({children, name, path}) => (
            <div key={path} className="link-inner">
              <Link className={`link ${location.pathname === path ? 'active' : ''}`} to={path} onClick={handleClose}>
                {name}
              </Link>
              {children &&
                children.map(({name: childName, path: childPath}) => (
                  <div key={childPath} className="link-inner">
                    <Link className={`link ${location.pathname === childPath ? 'active' : ''}`} to={childPath} onClick={handleClose}>
                      {childName}
                    </Link>
                  </div>
                ))
              }
            </div>
          ))}
        </StyledMenuLinks>
      </animated.div>
    </StyledMenu>
  );
}
const StyledMenu = styled.div`
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 1000;
  color: ${({ theme }) => theme.colorWhite};
  display: flex;
  justify-content: flex-end;
  &.hidden {
    pointer-events: none;
  }
  .menu {
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    width: 200px;
    z-index: 1002;
    background-color: ${({ theme }) => theme.colorPrimary};
    .header {
      text-align: right;
      padding: 5px;
      .close-button {
        height: 30px;
        width: 30px;
        padding: 2px;
        cursor: pointer;
        &:hover,
        &:active {
          color: ${({ theme }) => theme.colorSecondary};
        }
      }
    }
  }
  .overlay {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 1001;
    background-color: ${({ theme }) => theme.colorOpaque};
  }
`;
export default Menu;
